import listItem from "@/colorui/components/listitem"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
var that;
export default {
	components: {
		tuiTopDropdown,
		listItem
	},
	filters: {
		km: function(val) {
			if (val != null) {
				return (Number(val) / 1000).toFixed(2) + 'km';
			} else {
				return "";
			}
		}
	},
	data() {
		return {
			CustomBar: this.CustomBar,
			seller: false,
			isLoad: true,
			cardCur: 0,
			tabIndex: 1, //顶部筛选索引
			selectedName: ['地域', '排序', '阶段'],
			selectH1: 0,
			selectH2: 0,
			selectH3: 0,
			dropdownList1: [{
				value: 1,
				name: "地域1",
				selected: false
			}, {
				value: 2,
				name: "地域2",
				selected: false
			}, {
				value: 3,
				name: "地域3",
				selected: false
			}],
			dropdownList2: [{
				value: 0,
				name: "排序1",
				selected: false
			}, {
				value: 1,
				name: "排序2",
				selected: false
			}, {
				value: 2,
				name: "排序3",
				selected: false
			}],
			getTaskCategoryList: [],
			getTaskList: ["","","","","","","","","","","","","",""],
		};
	},
	onPullDownRefresh() {
		
	},
	methods: {
		px(num) {
			return uni.upx2px(num) + "px"
		},
		showDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 400;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 400;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 300;
			}
		},
		hideDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
		},
		dropdownItem2: function(e) {
			// console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[0] = data.name
			for (let i = 0; i < this.dropdownList1.length; i++) {
				this.dropdownList1[i].selected = false;
			}
			this.dropdownList1[data.index].selected = true;
			this.form.sort_1 = data.value
			this.selectH1 = 0
			this.hideDropdownList();
		},
		dropdownItem4: function(e) {
			// console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[1] = data.name
			for (let i = 0; i < this.dropdownList2.length; i++) {
				this.dropdownList2[i].selected = false;
			}
			this.dropdownList2[data.index].selected = true;
			this.form.sort_2 = data.value
			this.selectH2 = 0
			this.hideDropdownList();
		},
		dropdownItem5: function(e) {
			// console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[2] = data.name
			for (let i = 0; i < this.getTaskCategoryList.length; i++) {
				this.getTaskCategoryList[i].selected = false;
			}
			this.getTaskCategoryList[data.index].selected = true;
			this.form.sort_3 = data.value
			this.selectH3 = 0
			this.hideDropdownList();
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			// console.log(index)
			this.tabIndex = index;
			if (index == 1) {
				if (this.selectH1 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 2) {
				if (this.selectH2 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 3) {
				if (this.selectH3 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
		},
	},
	onReachBottom() {
		setTimeout(function(){
			for (var i = 0; i < 10; i++) {
				that.getTaskList.push("")
			}
		},2000)
	},
	mounted() {
		that = this;
		// uni.pageScrollTo({
		// 	scrollTop: 0
		// })
		this.dropdownList1[0].selected = true;
		this.dropdownList2[0].selected = true;
	}
}
